<template>
  <div class="layout--main" :class="[navbarClasses, footerClasses, {'app-page': isAppPage}]">
    <nv-sidebar :sidebarItems="sidebarItems" :logo="logo()" :title="$t('PRODUCT_NAME')" parent=".layout--main"/>

    <vs-popup :active.sync="sessionPopup" :title="$t('SESSION_NOTICE')">
      <div class="text-center">
        <p class="mb-4">{{ $t('SESSION_EXPIRES_SOON') }}</p>
        <p class="mb-4 text-primary text-5xl">{{ this.sessionTimer }}</p>
        <vs-button class="p-3" @click="extendSession" type="filled">{{ $t('EXTEND') }}</vs-button>
      </div>
    </vs-popup>

    <div id="content-area" :class="[contentAreaClass, {'show-overlay': bodyOverlay}]">

      <div id="content-overlay"></div>

      <div class="content-wrapper">

        <navbar :navbarColor="navbarColor" :class="[{'text-white': isNavbarDark && !isThemeDark}, {'text-base': !isNavbarDark && isThemeDark}]"/>

        <div class="router-view">
          <div class="router-content" :class="{'mt-0': navbarType === 'hidden'}">
            <transition :name="routerTransition">
              <div class="router-header flex flex-wrap items-center mb-6" v-if="$route.meta.title || $route.meta.breadcrumbs">
                <div class="content-area__heading" :class="{'pr-4 border-0 md:border-r border-t-0 border-b-0 border-l-0 border-solid border-grey-light' : $route.meta.breadcrumbs}">
                  <h2 class="mb-1">{{ $t(routeTitle) }}</h2>
                </div>

                <nv-breadcrumb class="ml-4 md:block hidden" v-if="$route.meta.breadcrumbs"/>

                <div class="ml-auto hidden flex flex-wrap-reverse items-center" v-if="$route.meta.tools">
                  <template v-for="(tool, index) in $route.meta.tools">
                    <vs-button :key="index" class="p-3 ml-2" :class="{active : activeToolBtn === tool.toolId}" @click="eventHubEmit(tool.toolId)" type="line" :icon="tool.icon" icon-pack="far">
                      <span v-if="tool.title">{{ $t(tool.title) }}</span>
                    </vs-button>
                  </template>
                </div>
              </div>
            </transition>
            <div class="content-area__content">
              <back-to-top bottom="5%" visibleoffset="200">
                <vs-button icon-pack="feather" icon="icon-arrow-up" class="shadow-lg"/>
              </back-to-top>
              <transition :name="routerTransition" mode="out-in">
                <router-view @changeRouteTitle="changeRouteTitle"></router-view>
              </transition>
            </div>
          </div>
        </div>

      </div>

      <app-footer></app-footer>
    </div>
  </div>
</template>

<script>
import NvSidebar from '@/components/nv-sidebar/NvSidebar.vue'
import Navbar from '@/components/navbar/Navbar.vue'
import AppFooter from '@/components/app-footer/AppFooter.vue'
import themeConfig from '@/../themeConfig.js'
import sidebarItems from '@/navigation/SidebarItems.js'
import BackToTop from 'vue-backtotop'
import AppBrandingMixin from '@/mixins/commons/AppBrandingMixin'
import SecurityService from '@/services/nucleus/SecurityService'

export default {
  mixins: [AppBrandingMixin],
  data () {
    return {
      navbarType: themeConfig.navbarType || 'floating',
      navbarColor: themeConfig.navbarColor || '#fff',
      footerType: themeConfig.footerType || 'static',
      routerTransition: themeConfig.routerTransition || 'none',
      isNavbarDark: false,
      routeTitle: this.$route.meta.title,
      sidebarItems: sidebarItems,
      disableCustomizer: themeConfig.disableCustomizer,
      windowWidth: window.innerWidth, // width of windows
      activeToolBtn: 'ListLayout',
      extendSessionTimer: process.env.VUE_APP_EXTEND_SESSION_TIMER,
      sessionPopup: false
    }
  },
  timers: {
    sessionCountDown: {
      time: 1000,
      autostart: true,
      repeat: true
    }
  },
  computed: {
    isAppPage () {
      if (this.$route.path.includes('/apps/')) return true
      else return false
    },
    isThemeDark () { return this.$store.state.theme === 'dark' },
    sidebarWidth () {
      return this.$store.state.sidebarWidth
    },
    bodyOverlay () {
      return this.$store.state.bodyOverlay
    },
    contentAreaClass () {
      let contentAreaCssClass = ''
      if (this.sidebarWidth === 'default') contentAreaCssClass = 'content-area-default'
      else if (this.sidebarWidth === 'reduced') contentAreaCssClass = 'content-area-reduced'
      else if (this.sidebarWidth) contentAreaCssClass = 'content-area-full'
      return contentAreaCssClass
    },
    navbarClasses () {
      return {
        'navbar-hidden': this.navbarType === 'hidden',
        'navbar-sticky': this.navbarType === 'sticky',
        'navbar-static': this.navbarType === 'static',
        'navbar-floating': this.navbarType === 'floating'
      }
    },
    footerClasses () {
      return {
        'footer-hidden': this.footerType === 'hidden',
        'footer-sticky': this.footerType === 'sticky',
        'footer-static': this.footerType === 'static'
      }
    },
    sessionTimer: function () {
      let date = new Date(null)
      date.setSeconds(this.$store.state.session.sessionValidity)
      return date.toISOString().substr(14, 5)
    }
  },
  methods: {
    changeRouteTitle (title) {
      this.routeTitle = title
    },
    updateNavbarColor (val) {
      this.navbarColor = val
      if (val === '#fff') this.isNavbarDark = false
      else this.isNavbarDark = true
    },
    handleWindowResize (event) {
      this.windowWidth = event.currentTarget.innerWidth
      this.setSidebarWidth()
    },
    setSidebarWidth () {
      if (this.windowWidth < 1200) {
        this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', false)
        this.$store.dispatch('updateSidebarWidth', 'no-sidebar')
      } else if (this.windowWidth < 1200) {
        this.$store.dispatch('updateSidebarWidth', 'reduced')
      } else {
        this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', true)
      }
    },
    eventHubEmit (toolId) {
      this.activeToolBtn = toolId
      this.$eventHub.emit('toolClicked', toolId)
    },
    sessionCountDown () {
      if (this.$store.getters.isSessionAuthenticated()) {
        this.$store.dispatch('decrementSessionValidity', {})
        this.sessionPopup = this.$store.state.session.sessionValidity < this.extendSessionTimer
      } else {
        this.sessionPopup = false
        this.$router.push({ name: 'Login' })
      }
    },
    extendSession () {
      this.$timer.stop('sessionCountDown')
      this.sessionPopup = false
      let self = this
      SecurityService.sessionToken().then(function (response) {
        SecurityService.poll({}).then(function (response) {
          if (response.data.sessionId) {
            self.$store.dispatch('setSession', {
              sessionId: response.data.sessionId,
              sessionValidity: response.data.sessionValidity,
              sessionToken: response.data.sessionToken
            })
            self.$timer.start('sessionCountDown')
          }
        })
      })
    }
  },
  components: {
    NvSidebar,
    Navbar,
    AppFooter,
    BackToTop
  },
  created () {
    this.setSidebarWidth()
    if (this.navbarColor === '#fff' && this.isThemeDark) {
      this.updateNavbarColor('#10163a')
    } else {
      this.updateNavbarColor(this.navbarColor)
    }
  },
  watch: {
    '$route' () {
      this.routeTitle = this.$route.meta.title
      this.activeToolBtn = 'ListLayout'
    },
    isThemeDark (val) {
      if (this.navbarColor === '#fff' && val) {
        this.updateNavbarColor('#10163a')
      } else {
        this.updateNavbarColor('#fff')
      }
    }
  }
}
</script>
