<template>
  <div class="relative">
    <div class="nv-navbar-wrapper">
      <vs-navbar class="nv-navbar navbar-custom" :color="navbarColor" :class="classObj">

        <!-- SM - OPEN SIDEBAR BUTTON -->
        <feather-icon class="sm:inline-flex xl:hidden cursor-pointer mr-2" icon="MenuIcon" @click.stop="showSidebar"></feather-icon>

        <!-- user -->
        <div class="the-navbar__user-meta flex items-center">
          <span class="far fa-user fa-lg mr-2 hidden sm:inline-block"></span>
          <div class="text-center leading-tight">
            <span class="mr-2 hidden sm:inline-block">{{ $store.state.user.userFullName }}</span>
          </div>
        </div>

        <vs-spacer></vs-spacer>

        <!-- Tasks -->
        <div class="chip-wrap mr-4">
          <router-link class="text-grey-darker" :to="{ name: 'WorkflowTasks', params: {} }" :title="$t('TASKS')">
            <span class="far fa-inbox fa-lg cursor-pointer"></span>
            <vs-chip v-if="$store.state.workflowTasks > 0" class="custom-chip" color="primary">{{ $store.state.workflowTasks }}</vs-chip>
          </router-link>
        </div>

        <!-- profile -->
        <div class="mr-4">
          <router-link class="text-grey-darker" :to="{ name: 'Profile', params: {} }" :title="$t('PROFILE')">
            <span class="far fa-address-card fa-lg cursor-pointer"></span>
          </router-link>
        </div>

        <!-- SETTINGS -->
        <vs-dropdown vs-custom-content vs-trigger-click class="cursor-pointer">
          <span class="far fa-cog fa-lg cursor-pointer mr-4"></span>
          <vs-dropdown-menu>
            <ul style="min-width: 10rem">
              <li class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white" @click="nightMode('dark')">
                <i class="far fa-moon cursor-pointer pt-1"></i> <span class="ml-4">{{ $t('NAV_NIGHT_MODE') }}</span>
              </li>
              <li class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white" @click="toggleFullScreen">
                <i class="far fa-window-maximize cursor-pointer pt-1"></i> <span class="ml-4">{{ $t('NAV_FULL_SCREEN') }}</span>
              </li>
            </ul>
          </vs-dropdown-menu>
        </vs-dropdown>

        <!-- Logout -->
        <i :title="$t('LOGOUT')" class="far fa-sign-out fa-lg text-danger cursor-pointer" @click="logout"></i>

      </vs-navbar>
    </div>
  </div>
</template>

<script>
import SecurityService from '@/services/nucleus/SecurityService'

export default {
  name: 'the-navbar',
  props: {
    navbarColor: {
      type: String,
      default: '#fff'
    }
  },
  data () {
    return {
      mainMenu: this.$store.state.mainMenu,
      showFullSearch: false,
      fullScreenMode: false
    }
  },
  computed: {
    // HELPER
    sidebarWidth () {
      return this.$store.state.sidebarWidth
    },
    breakpoint () {
      return this.$store.state.breakpoint
    },

    // NAVBAR STYLE
    classObj () {
      let classCssObj = ''
      if (this.sidebarWidth === 'default') classCssObj = 'navbar-default'
      else if (this.sidebarWidth === 'reduced') classCssObj = 'navbar-reduced'
      else if (this.sidebarWidth) classCssObj = 'navbar-full'

      return classCssObj
    },

    // BOOKMARK & SEARCH
    data () {
      return this.$store.state.mainMenu
    },
    starredPages () {
      return this.$store.state.starredPages
    },
    starredPagesLimited: {
      get () {
        return this.starredPages.slice(0, 10)
      },
      set (list) {
        this.$store.dispatch('arrangeStarredPagesLimited', list)
      }
    },
    starredPagesMore: {
      get () {
        return this.starredPages.slice(10)
      },
      set (list) {
        this.$store.dispatch('arrangeStarredPagesMore', list)
      }
    },

    // CART DROPDOWN
    cartItems () {
      return this.$store.state.eCommerce.cartItems.slice().reverse()
    }
  },
  methods: {
    showSidebar () {
      this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', true)
    },
    selected (item) {
      this.$router.push(item.url)
      this.showFullSearch = false
    },
    actionClicked (item) {
      // e.stopPropogation();
      this.$store.dispatch('updateStarredPage', { index: item.index, val: !item.highlightAction })
    },
    showNavbarSearch () {
      this.showFullSearch = true
    },
    showSearchbar () {
      this.showFullSearch = true
    },
    elapsedTime (startTime) {
      let x = new Date(startTime)
      let now = new Date()
      var timeDiff = now - x
      timeDiff /= 1000

      var seconds = Math.round(timeDiff)
      timeDiff = Math.floor(timeDiff / 60)

      var minutes = Math.round(timeDiff % 60)
      timeDiff = Math.floor(timeDiff / 60)

      var hours = Math.round(timeDiff % 24)
      timeDiff = Math.floor(timeDiff / 24)

      var days = Math.round(timeDiff % 365)
      timeDiff = Math.floor(timeDiff / 365)

      var years = timeDiff

      if (years > 0) {
        return years + (years > 1 ? ' Years ' : ' Year ') + 'ago'
      } else if (days > 0) {
        return days + (days > 1 ? ' Days ' : ' Day ') + 'ago'
      } else if (hours > 0) {
        return hours + (hours > 1 ? ' Hrs ' : ' Hour ') + 'ago'
      } else if (minutes > 0) {
        return minutes + (minutes > 1 ? ' Mins ' : ' Min ') + 'ago'
      } else if (seconds > 0) {
        return seconds + (seconds > 1 ? `${seconds} sec ago` : 'just now')
      }

      return 'Just Now'
    },
    outside: function () {
      this.showBookmarkPagesDropdown = false
    },
    logout () {
      let self = this
      SecurityService.logoutUser().then(function (response) {
        self.$store.dispatch('resetState')
        self.$router.push({ name: 'Login' })
      })
    },
    toggleFullScreen () {
      if (this.fullScreenMode === false) {
        let docElm = document.documentElement
        this.launchIntoFullscreen(docElm)
      } else {
        this.exitFullscreen()
      }
    },
    launchIntoFullscreen (element) {
      if (element.requestFullscreen) {
        element.requestFullscreen()
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen()
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen()
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen()
      }
      this.fullScreenMode = true
    },
    exitFullscreen () {
      if (document.exitFullscreen) {
        document.exitFullscreen()
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen()
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen()
      }
      this.fullScreenMode = false
    },
    checkFullScreenState () {
      let self = this
      let arr = ['fullscreenchange', 'webkitfullscreenchange', 'mozfullscreenchange', 'msfullscreenchange']
      arr.forEach(
        eventType => document.addEventListener(eventType, function () {
          var state = document.fullScreen || document.mozFullScreen || document.webkitIsFullScreen
          if (state) {
            self.fullScreenMode = true
          } else {
            self.fullScreenMode = false
          }
        }, false)
      )
    },
    nightMode () {
      if (this.$store.state.theme === 'dark') {
        this.$store.dispatch('updateTheme', 'light')
      } else {
        this.$store.dispatch('updateTheme', 'dark')
      }
    }
  },
  directives: {
    'click-outside': {
      bind: function (el, binding) {
        const bubble = binding.modifiers.bubble
        const handler = (e) => {
          if (bubble || (!el.contains(e.target) && el !== e.target)) {
            binding.value(e)
          }
        }
        el.__vueClickOutside__ = handler
        document.addEventListener('click', handler)
      },

      unbind: function (el) {
        document.removeEventListener('click', el.__vueClickOutside__)
        el.__vueClickOutside__ = null
      }
    }
  },
  mounted () {
    this.checkFullScreenState()
  }
}
</script>
